/* ================================================================================
  Quelques fonctions SASS utiles
================================================================================ */

/*
  Media query pour crans de dimension <=  la dimension spcifie dans $bp
*/
@mixin at-most($bp) {
	@media screen and (max-width: $bp) {
		@content;
	}
}

/*
  Media query pour crans de dimension >=  la dimension spcifie dans $bp
*/
@mixin at-least($bp) {
	@media screen and (min-width: $bp) {
		@content;
	}
}